import React from "react";
import styles from "./FormControl.module.css";
import {required} from "../../utils/validator";
import {Field} from "redux-form";

export const FormControl = ({input, meta:{touched, error}, children, ...props}: any) => {
    const hasError = touched && error;
    return <div className={styles.formControl + " " + (hasError ? styles.error : "")}>
        <div>
            {children}
        </div>
        {hasError && <span>{error}</span>}
    </div>
}


export const Textarea = (props: any) => {
    const {input, meta, child, ...restProps} = props
    return <FormControl {...props}><textarea {...input} {...restProps}/></FormControl>

}

export const Input = (props: any) => {
    const {input, meta, child, ...restProps} = props
    return <FormControl {...props}><input {...input} {...restProps}/></FormControl>

}

export const createField = (placeholder: string | null, name: string, validators: any, component: any, props: any ={ }, text: string = '') => (
    <div><Field  component={component} name={name} placeholder={placeholder} validate={validators} {...props}/>{text}
    </div>
)