import {ProfileType} from "../components/Profile/ProfileContainer";
import {profileAPI, usersAPI} from "../api/Api";
import {Dispatch} from "redux";

export type PostsType = {
    id: number
    likesCount: number
    message: string
}

const ADD_POST = 'ADD-POST'
const SET_USER_PROFILE = 'SET-USER-PROFILE'
const SET_STATUS = 'SET_STATUS'
const DELETE_POST = 'DELETE_POST'

let initialState: initialStateType = {
    posts: [
        {id: 1, likesCount: 1, message: 'Hello'},
        {id: 2, likesCount: 2, message: 'Dima '},
        {id: 3, likesCount: 3, message: 'kak tu'},
        {id: 4, likesCount: 4, message: 'Nope'}
    ],
    profile: null,
    status: ""
}

export type initialStateType = {
    posts: Array<PostsType>
    profile: null | ProfileType
    status: string
}

type ActionsType = addPostActionType | setUserProfile | setStatusActionType | deletePostActionType

const profileReducer = (state: initialStateType = initialState, action: ActionsType): initialStateType => {
    switch (action.type) {
        case ADD_POST: {
            let newPost: PostsType = {
                id: 5,
                message: action.newPostText,
                likesCount: 0
            }
            return {
                ...state,
                posts: [...state.posts, newPost],
            }
        }
        case DELETE_POST: {
            return {...state, posts: state.posts.filter(p => p.id != action.postId)}
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }
        default:
            return state;

    }
}

type addPostActionType = ReturnType<typeof addPost>
type deletePostActionType = ReturnType<typeof deletePost>
type setUserProfile = ReturnType<typeof setUserProfil>
type setStatusActionType = ReturnType<typeof setStatus>


export const addPost = (newPostText: string) => {
    return {type: ADD_POST, newPostText} as const
}
export const deletePost = (postId: number) => {
    return {type: DELETE_POST, postId} as const
}
export const setStatus = (status: string) => {
    return {type: SET_STATUS, status} as const
}

const setUserProfil = (profile: ProfileType) => {
    return {type: SET_USER_PROFILE, profile} as const
}

export const getUserProfile = (userId: number) => async (dispatch: Dispatch<setUserProfile>) => {
    let response = await usersAPI.getProfile(userId)
    dispatch(setUserProfil(response.data))

}

export const getStatus = (userId: number) => async (dispatch: Dispatch<setStatusActionType>) => {
    let response = await profileAPI.getStatus(userId)
    dispatch(setStatus(response.data))
}
export const updateStatus = (status: string) => async (dispatch: Dispatch<setStatusActionType>) => {
    let response = await profileAPI.updateStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(setStatus(status))
    }
}

export default profileReducer;