import {usersAPI} from "../api/Api";
import {Dispatch} from "redux";
import {updateObjectInArray} from "../utils/object-helpers";


export type UserType = {
    id: number
    followed: boolean
    name: string
    status: string
    photos: {
        "small": string | undefined
        "large": null
    }
    location: {
        city: string
        country: string
    }
}

let initialState: initialStateType = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: []
}

export type initialStateType = {
    users: any //Array<UserType>
    pageSize: number
    totalUsersCount: number
    currentPage: number
    isFetching: boolean
    followingInProgress: Array<number>
}

const usersReducer = (state: initialStateType = initialState, action: ActionsType): initialStateType => {
    switch (action.type) {
        case "FOLLOW":
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, 'id', {followed: true})
            }
        case "UNFOLLOW":
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userID, 'id', {followed: false})
            }
        case "SET-USERS":
            return {
                ...state, users: action.users
            }
        case "SET-CURRENT-PAGE":
            return {
                ...state, currentPage: action.currentPage
            }
        case "SET-USERS-TOTAL-COUNT":
            return {
                ...state, totalUsersCount: action.count
            }
        case "SET-TOGGLE":
            return {
                ...state, isFetching: action.isFetching
            }
        case "TOGGLE-IS-FOLLOWING-PROGRESS":
            return {
                ...state, followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userID]
                    : state.followingInProgress.filter((id: number) => id !== action.userID)
            }
        default:
            return state;
    }
}

type followActionType = ReturnType<typeof followSuccess>
type unfollowActionType = ReturnType<typeof unfollowSuccess>
type setUsersActionType = ReturnType<typeof setUsers>
type setCurrentPageType = ReturnType<typeof setCurrentPage>
type setUsersTotalCountType = ReturnType<typeof setTotalUsersCount>
type setToggleType = ReturnType<typeof toggleIsFetching>
type toggleFollowingType = ReturnType<typeof toggleFollowingProgress>

type ActionsType =
    followActionType
    | unfollowActionType
    | setUsersActionType
    | setCurrentPageType
    | setUsersTotalCountType
    | setToggleType
    | toggleFollowingType

export const followSuccess = (userID: number) => {
    return {type: "FOLLOW", userID: userID} as const
}
export const unfollowSuccess = (userID: number) => {
    return {type: "UNFOLLOW", userID: userID} as const
}
export const setUsers = (users: Array<UserType>) => {
    return {type: "SET-USERS", users: users} as const
}
export const setCurrentPage = (currentPage: number) => {
    return {type: "SET-CURRENT-PAGE", currentPage: currentPage} as const
}
export const setTotalUsersCount = (totalUsersCount: number) => {
    return {type: "SET-USERS-TOTAL-COUNT", count: totalUsersCount} as const
}
export const toggleIsFetching = (isFetching: boolean) => {
    return {type: "SET-TOGGLE", isFetching} as const
}
export const toggleFollowingProgress = (isFetching: boolean, userID: number) => {
    return {type: "TOGGLE-IS-FOLLOWING-PROGRESS", isFetching, userID} as const
}


export const requestUsers = (page: number, pageSize: number) => {
    return async (dispatch: Dispatch<setToggleType | setUsersActionType | setUsersTotalCountType | setCurrentPageType>) => {
        dispatch(toggleIsFetching(true))
        dispatch(setCurrentPage(page))
        let data = await usersAPI.getUsers(page, pageSize)
        dispatch(toggleIsFetching(false))
        dispatch(setUsers(data.items))
        dispatch(setTotalUsersCount(data.totalCount))
    }
}
export const followUnfollowFlow = async (dispatch: any, userId: number, apiMethod: any, actionCreator: any) => {
    dispatch(toggleFollowingProgress(true, userId))
    let response = await apiMethod(userId)
    if (response.data.resultCode === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowingProgress(false, userId))

}

export const follow = (userId: number) => {
    return async (dispatch: Dispatch<followActionType>) => {
        followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(usersAPI), followSuccess)
    }
}
export const unfollow = (userId: number) => {
    return async (dispatch: Dispatch<unfollowActionType>) => {
        followUnfollowFlow(dispatch, userId, usersAPI.unfollow.bind(usersAPI), unfollowSuccess)
    }
}


export default usersReducer;