import {authAPI} from "../api/Api";
import {Dispatch} from "redux";
import {stopSubmit} from "redux-form";

type InitialStateType = {
    id: number | null
    email: null | string
    login: null | string
    isAuth: boolean
}

let initialState: InitialStateType = {
    id: null,
    email: null,
    login: null,
    isAuth: false
}

const SET_USER_DATA = 'network/auth/SET-USER-DATA'

const authReducer = (state: InitialStateType = initialState, action: ActionTypes): InitialStateType => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
                isAuth: true
            }
        default:
            return state
    }
}

type setUserDataActionType = ReturnType<typeof setAuthUserData>
type ActionTypes = setUserDataActionType


export const setAuthUserData = (id: null | number, email: null | string, login: null | string, isAuth: null | boolean) => {
    return {type: SET_USER_DATA, payload: {id, email, login}} as const
}
export const getAuthUserData = () => async (dispatch: Dispatch<setUserDataActionType>) => {
    let response = await authAPI.me()
    if (response.data.resultCode === 0) {
        let {id, login, email} = response.data.data
        dispatch(setAuthUserData(id, email, login, true))
    }
}
export const login = (email: string, password: string, rememberMe: boolean) => async (dispatch: Dispatch<any>) => {
    let response = await authAPI.login(email, password, rememberMe)
    if (response.data.resultCode === 0) {
        dispatch(getAuthUserData())
    } else {
        let message = (response.data.message.length > 0 ? response.data.message[0] : 'Common error')
        dispatch(stopSubmit('login', {_error: message}))
    }

}
export const logout = () => async (dispatch: Dispatch<setUserDataActionType>) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false))
    }
}

export default authReducer;