import {getAuthUserData} from "./Auth-reducer";
import {Dispatch} from "redux";


let initialState = {
    initialized: false
}

type InitialStateType = {
    initialized:boolean
}

const appReducer = (state: InitialStateType = initialState, action: ActionTypes): InitialStateType => {
    switch (action.type) {
        case 'SET-INITIALIZED':
            return {
                ...state,
                initialized: true
            }
        default:
            return state
    }
}

type initializedSuccessActionType = ReturnType<typeof initializedSuccess>

type ActionTypes = initializedSuccessActionType

export const initializedSuccess = () => {
    return {type: "SET-INITIALIZED" } as const
}

export const initializeApp = () => (dispatch: Dispatch<any>) => {
    let promise = dispatch(getAuthUserData())
    Promise.all([promise]).then(()=>{
        dispatch(initializedSuccess())
    })

}

export default appReducer;